import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import './styles/ResultsList.scss';

class ResultsList extends Component {

  render() {
    const spaceRegex = /(%20)+/g;
    const formattedQuery = this.props.location.search.replace(spaceRegex, ' ').substring(1);

    return(
      <div className="ResultsList">
        <p className="searchMessage"><strong className="searchMessageKeyword">{this.props.total}</strong> results from the ADCVD Cases were found for <strong className="searchMessageKeyword">{formattedQuery}.</strong></p>

        { this.props.results.map(item => {
          return (
            <div className="anItem" key={item.id}>
              <a href={item.url}>{item.country}, {item.product}</a>
              <p>{item.product}</p>
              <p>{item.case_number}</p>
            </div>
          )
        }) }
      </div>
    )
  }
}

export default withRouter(ResultsList);