import React, { Component } from 'react';
import { connect } from 'react-redux';
import './styles/FiltersContainer.scss';
import { TOGGLE_FILTER, CLEAR_FILTERS } from '../store/actions';
require('details-polyfill')

class FiltersContainer extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  render() {
    const categories = []
    Object.entries(this.props.aggregations).forEach(
      ([key, value]) => categories.push(key)
    )

    const FilterCategories = (
      categories.map((cat, i) => {
        return (
          <details className="FilterCategory" key={i}>
            <summary key={i}>{filterTitles[cat]}</summary>
            {this.props.aggregations[cat].map((sub, i) => {
              return (
                <label key={i}>
                  <input type="checkbox" name={cat} value={sub["key"]} key={i} onChange={this.props.onToggleFilter}/> {sub["key"]}: {sub["doc_count"]}
                </label>
              )
            })}
          </details>
        )
      })
    )

    return(
      <div className="FiltersContainer">
        { (this.props.aggregations !== {}) ? (
          <>
            <div><h2>Filter Results</h2><button onClick={this.props.onClearAll}>[Clear All]</button></div>
            {FilterCategories}
          </>
        ) : null }
      </div>
    )
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onToggleFilter: (event) => dispatch({type: TOGGLE_FILTER, name: event.target.name, value: event.target.value}),
    onClearAll: () => dispatch({ type: CLEAR_FILTERS})
  }
}

export default connect(null, mapDispatchToProps)(FiltersContainer);

const filterTitles = {
  case_numbers: "Case Numbers",
  commodities: "Commodities",
  countries: "Countries",
  hts_numbers: "HTS Numbers",
  products: "Products",
}
