export class TradeRepository {
  constructor(url, apiKey) {
    this.url = url;
    this.apiKey = apiKey;
  }

  async _getData(searchQuery, params="", activePage=1) {
    return fetch(`${this.url}?q=${searchQuery}&api_key=${this.apiKey}&${params}&size=10&offset=${(activePage-1)*10}`)
      .then(response => response.json())
      .catch(error => console.log(error))
  }

}