import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { CLEAR_FILTERS } from '../store/actions';
import { TradeRepository } from '../API/TradeRepository';
import config from '../API/config';
import Pagination from 'react-js-pagination';
import ReactPlaceholder from 'react-placeholder';
import "react-placeholder/lib/reactPlaceholder.css";
import ResultsList from './ResultsList';
import FiltersContainer from './FiltersContainer';
import './styles/ResultsContainer.scss';

class ResultsContainer extends Component {
  constructor(props) {
    super(props)
    this.state = {
      total: 0,
      offset: 0,
      activePage: 1,
      results: [],
      aggregations: {},
      loading: false,
    }
  };

  async fetchData() {
    await this.setState({ loading: true })
    const tradeRepository = await new TradeRepository(config.url, config.apiKey)
    
    let params = '';
    if (this.props.storedFilters) {
      Object.entries(this.props.storedFilters).forEach(
        ([key, value]) => {
          if (this.props.storedFilters[key].length > 0) {
            params += `&${key}=${this.props.storedFilters[key].map(item => item)}`
          }
        }
      )  
    }

    const tradeResponse = await tradeRepository._getData(this.props.location.search.substring(1), params, this.state.activePage)
    await this.setState({
      total: tradeResponse.total,
      offset: tradeResponse.offset,
      results: tradeResponse.results,
      aggregations: tradeResponse.aggregations,
      loading: false,
    })
  }

  componentDidMount() {
    this.fetchData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.location.search !== prevProps.location.search) {
      this.props.onClearAll(); // Clear the filters if submitted a new query
      document.querySelectorAll('details').forEach( el => el.open = false ); // And collapse all the filter categories
      this.fetchData();
    }
    if (this.props.storedFilters !== prevProps.storedFilters) {
      this.fetchData();
    }
  }

  handlePageChange(pageNumber) {
    this.setState({ activePage: pageNumber }, () => this.fetchData());
  };

  render() {
    return (
      <div className="ResultsContainer">

        <FiltersContainer aggregations={this.state.aggregations}/>

        <ReactPlaceholder 
          type='text' 
          showLoadingAnimation={true} 
          ready={!this.state.loading} 
          rows={6} style={{ width: 250, margin: '2em 0 0.5em 1em' }} color='#E0E0E0'
        >

          <ResultsList total={this.state.total} results={this.state.results}/>

        </ReactPlaceholder>

        { (this.state.total > 0) ? (
          <Pagination 
            activePage={this.state.activePage}
            totalItemsCount={this.state.total}
            itemsCountPerPage={10}
            firstPageText="<<"
            prevPageText="<"
            nextPageText=">"
            lastPageText=">>"
            onChange={(pageNumber) => this.handlePageChange(pageNumber)}
          />
        ) : null }
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    storedFilters: state,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onClearAll: () => dispatch({ type: CLEAR_FILTERS})
  }
}

export default ResultsContainer = withRouter(connect(mapStateToProps, mapDispatchToProps)(ResultsContainer));
